/* Login */

.moloni-login {
  &--wrapper {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
  }

  &--card {
    width: 30rem;
    padding: 2.5rem;
  }

  &--error {
    font-size: 12px;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #F44336;
    color: white;
    text-shadow: 1px 1px 0 #F44336;
    margin-top: -15px;
    margin-bottom: 15px;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    transition: box-shadow .25s;
    cursor: pointer
  }

  &--block {
    margin-top: 2rem;

    &:first-child {
      margin-top: 0;
    }
  }

  &--image {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
  }

  &--button {
    width: 100%;
  }
}
